import React from 'react'
import PropTypes from 'prop-types'

const Pricing = ({ data }) => (
  data.map(price => (
    <div key={price.plan} className="w-1/3 p-4">
      <div className="shadow-lg rounded overflow-hidden">
          <div className="px-6 py-4">
            <div className="font-bold text-xl mb-2">{price.plan}</div>
            <p className="text-gray-700 text-base">
              {price.description}
            </p>
          </div>
          <div className="px-6 py-4">
            {price.items.map(item => (
              <span key={item} className="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 last:mr-0">#{item}</span>
            ))}
          </div>
          <div className="px-6 py-4">
            <div className="text-sm">
              <p className="text-gray-900 leading-none">{price.price}</p>
            </div>
          </div>
      </div>
    </div>
  ))
)

Pricing.propTypes = {
  data: PropTypes.arrayOf(
    PropTypes.shape({
      plan: PropTypes.string,
      price: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
      description: PropTypes.string,
      items: PropTypes.array,
    })
  ),
}

export default Pricing
